$backgroundColor: #f5f5f5;

html, body {
  font-size: 15px;
  font-family: cursive;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: $backgroundColor;
  height: 100%;
#history{
  display: flex;
    flex-direction: row-reverse;
    height: 100px;
}
  #root {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  footer {
    background: darken($backgroundColor, 50);
    border-top: 1px solid #eee;
    padding: 1em 3em;
    margin: 0;
  }

  header {
    background: darken($backgroundColor, 75);
    border-bottom: 1px solid #eee;
    color: #eee;
    padding: 0;
    margin: 0;
    text-align: center;
  }
   div{
  margin: 20px;
  text-align: center;
  color: rgb(33, 19, 33);
  // background-color: aqua;
} 
}